/* 0. Reset, variables, mixins, etc ================================= */
@import 'reset';

$column-1-1: 100%;
$column-1-2: 1 / 2 * 100%;
$column-1-3: 1 / 3 * 100%;

$tablet-breakpoint: 640px;
$desktop-breakpoint: 960px;

$primary-color: #fff;
$secondary-color: #000;
$accent-color: #ff0088;

/* 1. Main Layout ================================= */

*{
	box-sizing: border-box;
}

body{
	background: $primary-color;
	font-family: Helvetica, Arial, sans-serif;
	color: $secondary-color;
}

h1{
	font-size: 2.8rem;
	font-weight: 100;
}

h2{
	font-size: 1.8rem;
	font-weight: 100;
}

h3{
	font-size: 1.2rem;
}

p{
	padding: 10px;
}

header{
	>img{
		width: 100%;
		height: 200px;
		object-fit: cover;
	}
}

/* 2. Content ================================= */

.article-body{
	display: flex;
	flex-wrap: wrap;
	
	p{
		flex-basis: $column-1-1;
	}

	@media only screen and (min-width: $tablet-breakpoint){
		p{
			flex-basis: $column-1-2;
		}
	}
}

.media{
	display: flex;
	align-items: center;

	.media-figure{
		margin-right: 1rem;
	}

	.media-content{
		flex: 1;
	}
}

.author{
	img{
		width: 60px;
		border-radius: 60px;
	}

	h3{
		color: $accent-color;
	}
}

.article-image{
	position: relative;
	width: 100%;
	display: flex;
	overflow: hidden;

	img{
		width: 100%;
		height: 100%;
		transition: 0.7s;
	}

	figcaption{
		position: absolute;
		bottom: 0;
		width: 100%;
		padding: 10px;
		background: #555;
		color: white;
		transform: translateY(100%);
		transition: 0.3s;
	}

	&:hover{
		img{
			transform: scale(1.2);
		}

		figcaption{
			transform: translateY(0);
		}
	}

}





